<template>
  <div class="departamento__filters">
    <div class="departamento__filters--content">
      <div class="departamento__filters--item">
        <filter-button
          filterName="all"
          :selectedFilter="selectedCategory"
          v-on:filtered="filterList"
        >
          Усі документи
        </filter-button>
      </div>
      <div 
        v-for="filter of $store.state.docs.categories"
        :key="filter.englishName"
        class="departamento__filters--item"
      >
        <filter-button
          :filterName="filter.englishName"
          :selectedFilter="selectedCategory"
          v-on:filtered="filterList"
        >
          {{ filter.name }}
        </filter-button>
      </div>
    </div>
  </div>
</template>
<script>
import FilterButton from '@/components/FilterButton'

export default {
  name: 'Filters',
  components: {
    FilterButton,
  },
  props: {
    filters: {
      required: true,
      type: Array,
    }
  },
  methods: {
    filterList(emittedFilterName) {
      this.$store.commit('docs/setSelectedCategory', emittedFilterName)
    }
  },
  computed: {
    selectedCategory() {
      return this.$store.state.docs.selectedCategory;
    }
  }
}
</script>