<template>
  <router-link 
      v-if="isDocItemShown"
      class="departamento__doc-list__item" 
      :class="{'departamento__doc-list__item--card': type === 'card'}"
      :to="link"
  >
    <v-sheet
      class="departamento__doc-list__item--sheet"
      rounded
      v-ripple="{'class': 'departamento--primary__color'}"
      elevation="3"
    >
      <slot></slot>
    </v-sheet>
  </router-link>
</template>
<script>
export default {
  name: "DocListItem",
  props: {
    type: {
      required: true,
      type: String,
      validator: t => ["card", "list"].includes(t)
    },
    category: {
      required: true,
      type: String,
      validator: category => ["all", "forTeachers", "forDepartment", "forms", "dek"].includes(category)
    },
    link: {
      required: true,
      type: String
    }
  },
  computed: {
    selectedCategory() {
      return this.$store.state.docs.selectedCategory;
    },
    isDocItemShown() {
      return this.category === this.selectedCategory || this.selectedCategory === 'all';
    },
  }
}
</script>