<template>
  <div class="departamento__filter-button">
    <button
      :class="{ 'filter-button--active': selectedFilter === filterName }"
      @click="filterList"
    >
      <slot></slot>
    </button>
  </div>
</template>
<script>
export default {
  name: 'FilterButton',
  props: {
    filterName: {
      required: true,
      // todo: what is DEK? 
      validator: filter => ["all", "forTeachers", "forDepartment", "forms", "dek"].includes(filter)
    },
    selectedFilter: {
      required: true
    }
  },
  methods: {
    filterList() {
      this.$emit('filtered', this.filterName)
    }
  }
}
</script>