<template>
  <div class="departamento__doc-list">
    <div
        class="departamento__doc-list--content"
        :class="{
            'departamento__doc-list--cards': type === 'cards',
            'departamento__doc-list--list': type === 'list'
          }"
    >
      <doc-list-item
        v-for="item of mutatedList"
        :key="item.name"
        :type="type === 'cards' ? 'card' : type"
        :category="item.category"
        :link="item.link"
      >
        <!-- todo: refactor -->
        <!-- eslint-disable-next-line -->
        {{ item.name.length <= 40 ? item.name : item.name.slice(0, 37) + '...'}}
      </doc-list-item>
    </div>
  </div>
</template>
<script>
import DocListItem from '@/components/DocListItem';

export default {
  name: 'DocList',
  components: {
    DocListItem
  },
  props: {
    type: {
      required: true,
      type: String,
      validator: type => ["cards", "list"].includes(type)
    },
    items: {
      required: true,
      type: Array
    }
  },
  computed: {
    mutatedList() {
      return this.items.map(({categoryUid, ...itemAttributes}) => {
        return {...itemAttributes, category: this.categories.find(category => category.uid === categoryUid).englishName }
      })
    },
    categories() {
      return this.$store.state.docs.categories;
    }
  }
}
</script>